<template>
    <div class="theme-btn">
      <label>
        <input
            v-model="themeMode"
            @change="changeTheme"
            type="checkbox"
            :checked="themeMode"
        >
        <span class="theme-btn__slider"></span>
      </label>
    </div>
</template>

<script>
import {computed, ref, inject} from "vue";

export default {
  name: 'theme-btn',
  setup(props, { emit }) {
    const mode = inject('mode');

    const themeMode = ref(mode === 'dark');
    function changeTheme() {
      emit('changeTheme', {
        theme: themeMode.value,
      })
    }

    return {
      themeMode,
      changeTheme,
    }
  }
}
</script>
